import { createStore } from 'vuex';

const store = createStore({
    state() {
        return {
            loginData: null
        }
    },
    mutations: {
        setLoginData(state, valor){
            state.loginData = valor;
        }
    },
    actions: {
        login(context, valor){
            context.commit('setLoginData', valor);
        }
    },
    getters: {
        getloginData(state){
            return state.loginData;
        }
    }
}); 

export default store;