<template>
    <div class="card">
        <form @submit.prevent="doLogin">
            <label for="usuario">Usuário: </label>
            <input type="text" name="usuario" v-model="loginData.usuario">
            <label for="senha">Usuário: </label>
            <input type="password" name="senha"  v-model="loginData.senha">
            <br>
            <button>Login</button>
        </form>
    <base-dialog :show="isError" title="An error occurred" @close="isError=false">
      <p>Ocorreu um erro</p>
    </base-dialog>
    </div>

</template>

<script>
import { mapActions } from 'vuex';

export default {
    data() {
        return {
            isError: false,
        loginData: {
            usuario: '',
            senha: '' 
        }
    }
    },
    methods: {
        doLogin(){

            //Fetch keycloak para buscar o token

            //Validar se foi autorizado ou não

           this.login(this.loginData); 
           this.$router.replace('/credencial');
        },
        ...mapActions(['login'])
    },
    mounted() {
           console.log(process.env.VUE_APP_VARIAVEL_A);
           console.log(process.env.VUE_APP_AUTH_CLI_USER);
           console.log(process.env.VUE_APP_AUTH_CLI_PASS)
        }

}
</script>
